/* Layout */
article aside {
  background: #920de90b;
  background: var(--color-secondary-accent);
  border-left: 4px solid #920de9;
  border-left: 4px solid var(--color-secondary);
  padding: 0.01rem 0.8rem;
}

body {
  background: #fff;
  background: var(--color-bg);
  color: #000;
  color: var(--color-text);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
  Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  font-family: var(--font-family);
  line-height: 1.5;
  line-height: var(--line-height);
  margin: 0;
  overflow-x: hidden;
  padding: 1rem 0;
}

footer,
header,
main {
  margin: 0;
  max-width: inherit;
  padding: 0;
}

hr {
  background-color: #e9e9e9;
  background-color: var(--color-bg-secondary);
  border: none;
  height: 1px;
  margin: 4rem 0;
}

section {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  justify-content: var(--justify-important);
}

section aside {
  border: 1px solid #e9e9e9;
  border: 1px solid var(--color-bg-secondary);
  border-radius: 5px;
  border-radius: var(--border-radius);
  box-shadow: 2px 2px 10px #f4f4f4;
  box-shadow: var(--box-shadow) var(--color-shadow);
  margin: 1rem;
  padding: 1.25rem;
  width: 285px;
  width: var(--width-card);
}

section aside:hover {
  box-shadow: 2px 2px 10px #e9e9e9;
  box-shadow: var(--box-shadow) var(--color-bg-secondary);
}

section aside img {
  max-width: 100%;
}

[hidden] {
  display: none;
}

/* Headers */
article header,
div header,
main header {
  padding-top: 0;
}

header {
  text-align: center;
  text-align: var(--justify-important);
}

header a b,
header a em,
header a i,
header a strong {
  margin-left: 0.5rem;
  margin-right: 0.5rem;
}

header nav img {
  margin: 1rem 0;
}

section header {
  padding-top: 0;
  width: 100%;
}

/* Nav */
nav {
  align-items: center;
  display: flex;
  font-weight: bold;
  justify-content: space-between;
}

nav ul {
  list-style: none;
  padding: 0;
}

nav ul li {
  display: inline-block;
  margin: 0 0.5rem;
  position: relative;
  text-align: left;
}

/* Nav Dropdown */
nav ul li:hover ul {
  display: block;
}

nav ul li ul {
  background: #fff;
  background: var(--color-bg);
  border: 1px solid #e9e9e9;
  border: 1px solid var(--color-bg-secondary);
  border-radius: 5px;
  border-radius: var(--border-radius);
  box-shadow: 2px 2px 10px #f4f4f4;
  box-shadow: var(--box-shadow) var(--color-shadow);
  display: none;
  height: auto;
  padding: 0.5rem 1rem;
  position: absolute;
  left: -2px;
  top: 1.7rem;
  width: auto;
}

nav ul li ul li,
nav ul li ul li a {
  display: block;
}

/* Typography */
code,
samp {
  background-color: #118bee15;
  background-color: var(--color-accent);
  border-radius: 5px;
  border-radius: var(--border-radius);
  color: #000;
  color: var(--color-text);
  display: inline-block;
  margin: 0 0.1rem;
  padding: 0 0.5rem;
}

details {
  margin: 1.3rem 0;
}

details summary {
  font-weight: bold;
  cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.5;
  line-height: var(--line-height);
}

mark {
  padding: 0.1rem;
}

ol li,
ul li {
  padding: 0.2rem 0;
}

p {
  margin: 0.75rem 0;
  padding: 0;
}

pre {
  margin: 1rem 0;
  max-width: 800px;
  max-width: var(--width-card-wide);
  padding: 1rem 0;
}

pre code,
pre samp {
  display: block;
  max-width: 800px;
  max-width: var(--width-card-wide);
  padding: 0.5rem 2rem;
  white-space: pre-wrap;
}

small {
  color: #999;
  color: var(--color-text-secondary);
}

sup {
  background-color: #920de9;
  background-color: var(--color-secondary);
  border-radius: 5px;
  border-radius: var(--border-radius);
  color: #fff;
  color: var(--color-bg);
  font-size: xx-small;
  font-weight: bold;
  margin: 0.2rem;
  padding: 0.2rem 0.3rem;
  position: relative;
  top: -2px;
}

/* Links */
a {
  color: #920de9;
  color: var(--color-secondary);
  display: inline-block;
  font-weight: bold;
  text-decoration: none;
}

a:hover {
  -webkit-filter: brightness(1.2);
          filter: brightness(1.2);
  -webkit-filter: brightness(var(--hover-brightness));
          filter: brightness(var(--hover-brightness));
  text-decoration: underline;
}

a b,
a em,
a i,
a strong,
button {
  border-radius: 5px;
  border-radius: var(--border-radius);
  display: inline-block;
  font-size: medium;
  font-weight: bold;
  line-height: 1.5;
  line-height: var(--line-height);
  margin: 0.5rem 0;
  padding: 1rem 2rem;
}

button {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
  Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  font-family: var(--font-family);
}

button:hover {
  cursor: pointer;
  -webkit-filter: brightness(1.2);
          filter: brightness(1.2);
  -webkit-filter: brightness(var(--hover-brightness));
          filter: brightness(var(--hover-brightness));
}

a b,
a strong,
button {
  background-color: #118bee;
  background-color: var(--color);
  border: 2px solid #118bee;
  border: 2px solid var(--color);
  color: #fff;
  color: var(--color-bg);
}

a em,
a i {
  border: 2px solid #118bee;
  border: 2px solid var(--color);
  border-radius: 5px;
  border-radius: var(--border-radius);
  color: #118bee;
  color: var(--color);
  display: inline-block;
  padding: 1rem 2rem;
}

/* Images */
figure {
  margin: 0;
  padding: 0;
}

figure img {
  max-width: 100%;
}

figure figcaption {
  color: #999;
  color: var(--color-text-secondary);
}

/* Forms */

button:disabled,
input:disabled {
  background: #e9e9e9;
  background: var(--color-bg-secondary);
  border-color: #e9e9e9;
  border-color: var(--color-bg-secondary);
  color: #999;
  color: var(--color-text-secondary);
  cursor: not-allowed;
}

button[disabled]:hover {
  -webkit-filter: none;
          filter: none;
}

form {
  border: 1px solid #e9e9e9;
  border: 1px solid var(--color-bg-secondary);
  border-radius: 5px;
  border-radius: var(--border-radius);
  box-shadow: 2px 2px 10px #f4f4f4;
  box-shadow: var(--box-shadow) var(--color-shadow);
  display: block;
  max-width: 800px;
  max-width: var(--width-card-wide);
  min-width: 285px;
  min-width: var(--width-card);
  padding: 1.5rem;
  text-align: left;
  text-align: var(--justify-normal);
}

form header {
  margin: 1.5rem 0;
  padding: 1.5rem 0;
}

input,
label,
select,
textarea {
  display: block;
  font-size: inherit;
  max-width: 800px;
  max-width: var(--width-card-wide);
}

input[type="checkbox"],
input[type="radio"] {
  display: inline-block;
}

input[type="checkbox"] + label,
input[type="radio"] + label {
  display: inline-block;
  font-weight: normal;
  position: relative;
  top: 1px;
}

input,
select,
textarea {
  border: 1px solid #e9e9e9;
  border: 1px solid var(--color-bg-secondary);
  border-radius: 5px;
  border-radius: var(--border-radius);
  margin-bottom: 1rem;
  padding: 0.4rem 0.8rem;
}

input[readonly],
textarea[readonly] {
  background-color: #e9e9e9;
  background-color: var(--color-bg-secondary);
}

label {
  font-weight: bold;
  margin-bottom: 0.2rem;
}

/* Tables */
table {
  border: 1px solid #e9e9e9;
  border: 1px solid var(--color-bg-secondary);
  border-radius: 5px;
  border-radius: var(--border-radius);
  border-spacing: 0;
  display: inline-block;
  max-width: 100%;
  overflow-x: auto;
  padding: 0;
  white-space: nowrap;
}

table td,
table th,
table tr {
  padding: 0.4rem 0.8rem;
  text-align: center;
  text-align: var(--justify-important);
}

table thead {
  background-color: #118bee;
  background-color: var(--color);
  border-collapse: collapse;
  border-radius: 5px;
  border-radius: var(--border-radius);
  color: #fff;
  color: var(--color-bg);
  margin: 0;
  padding: 0;
}

table thead th:first-child {
  border-top-left-radius: 5px;
  border-top-left-radius: var(--border-radius);
}

table thead th:last-child {
  border-top-right-radius: 5px;
  border-top-right-radius: var(--border-radius);
}

table thead th:first-child,
table tr td:first-child {
  text-align: left;
  text-align: var(--justify-normal);
}

table tr:nth-child(even) {
  background-color: #118bee15;
  background-color: var(--color-accent);
}

/* Quotes */
blockquote {
  display: block;
  font-size: x-large;
  line-height: 1.5;
  line-height: var(--line-height);
  margin: 1rem auto;
  max-width: 460px;
  max-width: var(--width-card-medium);
  padding: 1.5rem 1rem;
  text-align: center;
  text-align: var(--justify-important);
}

blockquote footer {
  color: #999;
  color: var(--color-text-secondary);
  display: block;
  font-size: small;
  line-height: 1.5;
  line-height: var(--line-height);
  padding: 1.5rem 0;
}

